<template>
<div>
  <el-table :data="userList.filter(data => !search || data.userList.toLowerCase().includes(search.toLowerCase()))" 
    border stripe :header-cell-class-name="headerBg">
    <el-table-column prop="id" label="序号" width="50" align="center">
    </el-table-column>
    <el-table-column prop="username" label="用户名" width="200">
    </el-table-column>
    <el-table-column prop="nickname" label="昵称" width="300">
    </el-table-column>
    <el-table-column prop="permission" label="管理权限" width="300">
    </el-table-column>
    <el-table-column  prop="" label="操作">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入关键字搜索"/>
      </template>
      <template slot-scope="{row,$index}">
        <el-button type="primary" style="padding:7px 10px;font-size:10px;" @click="editUserInfo(row)">修改<i class="el-icon-edit-outline" style="padding-left:2px;"></i></el-button>
        <el-button type="danger" style="padding:7px 10px;font-size:10px;" >删除<i class="el-icon-edit-outline" style="padding-left:2px;"></i></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div style="padding:10px 0;">
    <el-pagination
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
      style="text-align:center"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      :currentPage="currentPage"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
  <!-- 对话框 visible.sync -->
  <el-dialog title="用户信息修改" :visible.sync="dialogFormVisible">
    <el-form style="width:80%;" :model="currentUser">
      <el-form-item label="用户名" label-width="100px">
        <el-input v-model="currentUser.username" autocomplete="off" disabled></el-input>
      </el-form-item>
      <el-form-item label="用户昵称" label-width="100px">
        <el-input v-model="currentUser.nickname" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="用户签名" label-width="100px">
        <el-input v-model="currentUser.mydesc" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="用户权限" label-width="100px">
        <el-input v-model="currentUser.permission" autocomplete="off" disabled></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="editInfo()">修改</el-button>
      <el-button @click="dialogFormVisible = false">取 消</el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
  export default {
    data() {
      return {
        userList:[],
        total: 0,
        currentPage: 1,
        pageSize: 12,
        pageSizes: [12,15,20],
        headerBg:'headerBg',
        dialogFormVisible:false,
        currentUser:{},
        search: ''
      }
    },
    mounted(){
      this.getUserInfo()
      this.getUserInfoLength()
    },
    methods: {
      getUserInfo(){
        this.$axios.get('/admin/backgetUserInfo',{
          params:{
            pageSize:this.pageSize,
            currentPage:this.currentPage
          }
        }).then(res=>{
          if(res.status == 200){
            this.userList = res.data
            // console.log(this.userList);
          }
        })
      },
      //获取用户数量
      getUserInfoLength(){
        this.$axios.get('/admin/backgetUserInfoLength').then(res=>{
          if(res.status == 200){
            this.total = JSON.parse(res.data[0].length)
          }
        })
      },
      editUserInfo(row){
        this.currentUser = row
        this.dialogFormVisible = true
      },
      handleCurrentChange(pager){
        this.currentPage = pager
        this.getUserInfo()
      },
      handleSizeChange(limit){
        this.pageSize = limit
        this.getUserInfo()
      },
    },
  }
</script>
  
  <style scoped>
  
  </style>